
body {
  margin: 0;
  padding: 0;
    background: radial-gradient(ellipse at center,  #0a2e38  0%, #000000 70%);
    font-family: 'Inconsolata', monospace;
    overflow: hidden;
    height:100vh;
    font-size:15px;
    
}

.style{
    color:hsla(198, 100%, 74%, 0.78);
    text-shadow: 0 0 15px rgba(10, 175, 230, 1),  0 0 20px rgba(10, 175, 230, 0);
    text-align:center;
    margin:0 12% 10% 12%;
    -webkit-animation: text 6s infinite;
            animation: text 6s infinite

}
@-webkit-keyframes text {
  0%{-webkit-transform:scale(0);transform:scale(0);
      opacity:(0);}
  5% {-webkit-transform:scale(1);transform:scale(1)}
  80% {-webkit-transform:scale(1);transform:scale(1)}
  85%  {-webkit-transform:scale(1.2);transform:scale(1.2);
        opacity:1;}
  90%  {opacity:0;}
  100% {opacity:0;}
}
@keyframes text {
  0%{-webkit-transform:scale(0);transform:scale(0);
      opacity:(0);}
  5% {-webkit-transform:scale(1);transform:scale(1)}
  80% {-webkit-transform:scale(1);transform:scale(1)}
  85%  {-webkit-transform:scale(1.2);transform:scale(1.2);
        opacity:1;}
  90%  {opacity:0;}
  100% {opacity:0;}
}
Canvas{
  position:relative;
  left:32%;
}

